	      title: 'Security Chapter Capture the Flag Game Show',
	      type: 'Workshop',
	      description: 'Security Chapter gr sammen med Creative Crew for  lage Capture the Flag Game Show  To team med hpefulle vil bli stuet inn i hvert sitt rom hvor de har 45 minutter p seg til  lse capture the flag-oppgaver. Dette vil bli fanget p film slik at et panel i naborommet samtidig kan diskutere oppgavene og kommentere framgangen deres underveis. Etter 45 minutter vil man bytte deltakere, slik at flere forskjellige team vil f prvd seg p forskjellige oppgaver, totalt 3 runder  45 minutter. Antall deltakere vil kunne vre fra 4 (2 teams  2 personer) til maks 30 (6 teams  5 personer). De som ikke deltar er hjertelig velkommen som publikum, for dette blir show!'
